import React from "react";
import { Link } from "react-router-dom";

import styles from "./home.scss";


export default function index() {
  return (
    <div className={styles.home}>
      
      <Link className={styles.warrior} to="/warrior">
        <span></span>
      </Link>

      <Link className={styles.mage} to="/mage">
        <span></span>
      </Link>

      <Link className={styles.archer} to="/archer">
        <span></span>
      </Link>

      <Link className={styles.thief} to="/thief">
        <span></span>
      </Link>

      <Link className={styles.pirate} to="/pirate">
        <span></span>
      </Link>

      <Link className={styles.aran} to="/aran">
        <span></span>
      </Link>
      
    </div>
  );
}
